@import url(https://fonts.googleapis.com/css2?family=Dancing+Script:wght@600&family=Montserrat:wght@300;400&family=Seaweed+Script&display=swap);
html{
  scroll-behavior: smooth;
}

body {
  font-family: "Montserrat", sans-serif;
  overflow-x: hidden;
}
.brand-text {
  font-family: "Dancing Script", cursive;
  /* font-family: "Montserrat", sans-serif; */
  /* font-family: "Seaweed Script", cursive; */
}

.home {
  background: url(/React_app/static/media/justin-schwartfigure-azY9SgIIzuI-unsplash.915c57ab.jpg);
  background: url(/React_app/static/media/pexels-johannes-plenio-2259232.e2e29bb5.jpg);
  background-attachment: fixed;
  background-size: cover;
  background-position: center;
}
.overlay {
  background: rgba(0, 0, 0, 0.493);
}
.my_shadow {
  box-shadow: 5px 5px 15px rgba(20, 19, 19, 0.493);
  border-radius: 12px;
  padding: 5px 0;
  background: rgba(255, 255, 255, 0.459);
  /* color: rgba(255, 102, 0, 0.885); */
}
.touch,
.free {
  margin: 0 auto;
}

@media (max-width: 1200px) {
  .title {
    text-align: center;
  }
}
@media (min-width: 1200px) {
  .free {
    margin: 0 0;
  }
  .touch {
    margin-left: 2rem;
  }

  .title {
    margin-left: 2rem !important;
  }
  .title-text {
    margin-right: 5rem !important;
  }
}

.m{
    color: rgb(209, 63, 192);
}
.contactpage {
  background: url(/React_app/static/media/justin-schwartfigure-azY9SgIIzuI-unsplash.915c57ab.jpg);
  background: url("/React_app/static/media/pexels-amine-m'siouri-3284167.de5708f1.jpg");
  background: url(/React_app/static/media/pexels-johannes-plenio-2259232.e2e29bb5.jpg);
  background-position: bottom;
  background-attachment: fixed;
  background-size: cover;
}
.contactoverlay {
  background: rgba(0, 0, 0, 0.527);
  color: white;
}

#name,
#email,
#message {
  background-color: transparent;
  color: aliceblue !important;
}
.home_contact #name,
.home_contact #email,
.home_contact #message {
  background-color: transparent;
  color: rgb(20, 89, 150) !important;
}

.home_contact #name::-webkit-input-placeholder, .home_contact #email::-webkit-input-placeholder, .home_contact #message::-webkit-input-placeholder {
  color: rgb(35, 35, 36) !important;
}

.home_contact #name:-ms-input-placeholder, .home_contact #email:-ms-input-placeholder, .home_contact #message:-ms-input-placeholder {
  color: rgb(35, 35, 36) !important;
}

.home_contact #name::placeholder,
.home_contact #email::placeholder,
.home_contact #message::placeholder {
  color: rgb(35, 35, 36) !important;
}
#name::-webkit-input-placeholder, #email::-webkit-input-placeholder, #message::-webkit-input-placeholder {
  color: rgb(231, 231, 238) !important;
}
#name:-ms-input-placeholder, #email:-ms-input-placeholder, #message:-ms-input-placeholder {
  color: rgb(231, 231, 238) !important;
}
#name::placeholder,
#email::placeholder,
#message::placeholder {
  color: rgb(231, 231, 238) !important;
}

.map {
  overflow-x: scroll !important;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

